<template>
  <div class="horizontalSlideContainer">
    <input       type="range"
      min="-100"
      max="100"
      value="0"
      class="slider"
      v-model="myRange"
      
      @input="postValue()" />
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "HorizontalSlider",
    data() {
    return {
      myRange: 0
    };
  },
  methods: {
    postValue() {
      var endpoint = "http://localhost:8080/lego/motors/drive"
      axios.post(endpoint,{withCredentials: true}, {params:{
        motion: "angular",
        power: parseInt(this.myRange),
      }});
    }
  }
};
</script>

<style scoped>
html, body {
  height: 100%;
}
.horizontalSlideContainer {
  position: relative;
  width: 50%;
  max-width: 480px;
  min-width: 200px;
  z-index: 10;
  margin-top: 20px;
 
  display: block;
  margin-left: auto;
  margin-right: auto;

}
/* The slider itself */
.slider {
  -webkit-appearance: none; /* Override default CSS styles */
  width: 100%; /* Full-width */
  height: 50px; /* Specified height */
  border-radius: 50px;
  background: #d3d3d3; /* Grey background */
  outline: none; /* Remove outline */
  opacity: 0.7; /* Set transparency (for mouse-over effects on hover) */
  -webkit-transition: 0.2s; /* 0.2 seconds transition on hover */
  transition: opacity 0.2s;
}

/* Mouse-over effects */
.slider:hover {
  opacity: 1; /* Fully shown on mouse-over */
}

/* The slider handle (use -webkit- (Chrome, Opera, Safari, Edge) and -moz- (Firefox) to override default look) */
.slider::-webkit-slider-thumb {
  -webkit-appearance: none; /* Override default look */
  appearance: none;
  width: 75px; /* Set a specific slider handle width */
  height: 75px; /* Slider handle height */
  border-radius: 50%;
  background: #37c771; /* Green background */
  cursor: pointer; /* Cursor on hover */
}
</style>