<template>
  <div class="verticalSlideContainer">
    <input
      type="range"
      min="-75"
      max="75"
      value="0"
      class="slider"
      v-model="myRange"
      @input="postValue()"
    />
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "VerticalSlider",
  data() {
    return {
      myRange: 0
    };
  },
  methods: {
    postValue() {
      var endpoint = 'http://truckrest.westeurope.azurecontainer.io:8080/lego/motors/drive';
      var power = parseInt(this.myRange)
      if(Math.abs(this.value)>0) power+=25; //The truck only moves if the power is bigger than 25
      axios.post(endpoint,{}, {params:{
        motion: "linear",
        power: parseInt(this.myRange)*-1,
      }});
    }
  }
};
</script>

