<template>
    <div id="proximity" >
        <p>Distance: {{proximity}}cm</p>
    </div>
</template>

<script>

import axios from 'axios';
import SockJS from "sockjs-client";
import Stomp from "webstomp-client";
export default {
  name: 'ProximityWarning',
  data(){
      return{
          proximity:0
      }
  },
  methods:{
   connectStream: function(){
        var endpoint="http://truckrest.westeurope.azurecontainer.io:8080/lego/events/stream/500/proximity/last";
        var socket = new SockJS('http://truckrest.westeurope.azurecontainer.io:8080/lego/messages');
        var mystompClient = Stomp.over(socket);
        var topicWs;
        var self=this;
        
        axios.get(endpoint).then((response) => {
            console.log(response.data)
            topicWs = response.data;
            mystompClient.connect({}, function (frame) {
                //setConnected(true);
                mystompClient.subscribe(topicWs, function (data) {
                    try {
                        var obj = JSON.parse(data.body);
                            self.proximity=obj.distance;
                            if(obj.distance<10.0){
                                this.safetyBrake();
                            }
                    } catch (err) {
                        console.log(err);
                    }
                });
            });
        });
    },
    safetyBrake: function(){
    var endpoint = "http://truckrest.westeurope.azurecontainer.io:8080/lego/motors/drive";
      console.log("Object too close, breaking");
      axios.post(endpoint,{}, {params:{
        motion: "angular",
        power: 0,
      }});      
      axios.post(endpoint,{}, {params:{
        motion: "linear",
        power: 0,
      }});
    }
  },
  created(){
      this.connectStream()
  }
}
</script>

<style scoped>
html, body {
  height: 100%;
}
</style>